.pattern-creator {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

main, sidebar {
  -ms-flex: 1 1 50%;
      flex: 1 1 50%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

main {
  padding: 3rem;
  overflow-y: auto;
}

sidebar {
}

.two-column {
  display: -ms-flexbox;
  display: flex;
}

.two-column section {
  -ms-flex: 0 0 50%;
      flex: 0 0 50%;
  margin: 0;
}

.two-column section label {
  text-align: center;
}

section label {
  display: block;
  font-weight: 600;
}

section input {
  display: block;
  width: 100%;
  margin: 1rem 0 0 0;
}

section + section {
  margin-top: 2rem;
}

.input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.input-container span {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  font-weight: 400;
  margin-top: 1rem;
}

.input-container input {
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
  margin: 1rem 1rem 0 1rem;
}

.picker-container {
  margin: 1rem 0;
}

.picker-container .background-color {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 10rem;
  border: 1px solid #ddd;
  cursor: pointer;
}

.picker-container .background-color::before {
  position: absolute;
  left: 0;
  top: 0;
  content: '';
  width: 100%;
  height: 100%;
  border: 5px solid #fff;
  background-color: transparent;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.picker-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.image-uploader {
  display: block;
  width: 100%;
  height: 10rem;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  border: 1px solid #ddd;
}

.image-uploader input[type="file"] {
  display: none;
}

.chrome-picker {
  position: absolute;
}

@media (max-width: 960px) {

  main {
    padding: 1.5rem;
  }

  .pattern-creator {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
  }

  .chrome-picker {
    left: 0;
    right: 0;
    top: 50%;
    margin: -125px auto 0;
  }
}


